import React, { Component } from 'react';
import PropTypes from 'prop-types';

import './code.css';
import styles from './style.module.css';
import { Connection } from '../../configs';

export default class ApiContent extends Component {
	constructor() {
		super();
		this.state = {
			reqObj: '',
			resObj: '',
			bgStyle: '',
		};
	}

	componentDidUpdate(prevProps, prevState) {
		const { apiDetail } = this.props;
		if (prevProps.apiDetail !== apiDetail) {
			this.loadApiDetail(apiDetail);

			let bgStyle = '';
			const typeName = apiDetail.type.toLowerCase();

			if (typeName === 'post') {
				bgStyle = 'postBg';
			} else if (typeName === 'get') {
				bgStyle = 'getBg';
			} else if (typeName === 'put') {
				bgStyle = 'putBg';
			} else if (typeName === 'delete') {
				bgStyle = 'deleteBg';
			}

			this.setState({ bgStyle: bgStyle });
		}
	}

	loadApiDetail(api) {
		Connection.send('/ApiDoc' + api.url, {}).then((result) => {
			this.setState({ reqObj: JSON.stringify(result.reqData, null, 2) });
			this.setState({ resObj: JSON.stringify(result.resData, null, 2) });
		});
	}

	jsonColor(json) {
		if (typeof json != 'string') {
			json = JSON.stringify(json, undefined, 2);
		}

		if (json) {
			json = json
				.replace(/&/g, '&amp;')
				.replace(/</g, '&lt;')
				.replace(/>/g, '&gt;');
			return json.replace(
				/("(\\u[a-zA-Z0-9]{4}|\\[^u]|[^\\"])*"(\s*:)?|\b(true|false|null)\b|-?\d+(?:\.\d*)?(?:[eE][+\-]?\d+)?)/g,
				function (match) {
					var cls = 'number';
					if (/^"/.test(match)) {
						if (/:$/.test(match)) {
							cls = 'key';
						} else {
							cls = 'string';
						}
					} else if (/true|false/.test(match)) {
						cls = 'boolean';
					} else if (/null/.test(match)) {
						cls = 'null';
					}
					return '<span class="' + cls + '">' + match + '</span>';
				},
			);
		} else {
			return '';
		}
	}

	copyToClipboard = (jsonData) => {
		const el = document.createElement('textarea');
		el.value = jsonData;

		el.setAttribute('readonly', '');
		el.style.position = 'absolute';
		el.style.left = '-9999px';
		document.body.appendChild(el);

		el.select();
		document.execCommand('copy');
		document.body.removeChild(el);
	};

	render() {
		const apiDetail = this.props.apiDetail;

		return (
			<div className={styles.detailContainer}>
				<div className={styles.panelHead}>
					<a className={styles.btnAction + ' ' + this.state.bgStyle}>
						{apiDetail.type}
					</a>
					<div className={styles.headPath}>{apiDetail.url}</div>
				</div>
				<div className={styles.panelInnerContainer}>
					<div className={styles.respView}>
						<div>Request</div>
						<i
							className={'material-icons'}
							onClick={() => this.copyToClipboard(this.state.reqObj)}>
							content_copy
						</i>
					</div>

					<div
						className={styles.requestContainer}
						contentEditable={true}
						dangerouslySetInnerHTML={{
							__html: '<pre>' + this.jsonColor(this.state.reqObj) + '</pre>',
						}}
					/>

					<div className={styles.respView}>
						<div>Response</div>
						<i
							className={'material-icons'}
							onClick={() => this.copyToClipboard(this.state.resObj)}>
							content_copy
						</i>
					</div>

					<div
						className={styles.responseContainer}
						contentEditable='true'
						dangerouslySetInnerHTML={{
							__html: '<pre>' + this.jsonColor(this.state.resObj) + '</pre>',
						}}
					/>
				</div>
			</div>
		);
	}
}

ApiContent.propTypes = {
	apiDetail: PropTypes.object.isRequired,
};
