import React, { Component } from 'react';

import { AppContext, Util } from '../../base';
import ApiGroup from '../../components/apigroup';
import ApiContent from '../../components/apicontent';

import styles from './style.module.css';
import { Config, Connection } from '../../configs';
const { isEmpty } = Util;

export default class Login extends Component {
	static contextType = AppContext;

	constructor() {
		super();
		this.state = {
			appName: '',
			apiGroup: [],
			apiDetail: {},
		};
	}

	async componentDidMount() {
		let apiDoc = '';
		if (isEmpty(Config.apiUrl)) {
			Connection.send('/ApiDoc/api', {}).then((result) => {
				apiDoc = result;
				this.setState({ appName: apiDoc.appName, apiGroup: apiDoc.apiGroup });
			});
		} else {
			apiDoc = require('./api.json');
			this.setState({ appName: apiDoc.appName, apiGroup: apiDoc.apiGroup });
		}
	}

	loadApiDetail(api) {
		this.setState({ apiDetail: api });
	}

	render() {
		return !isEmpty(this.state.apiGroup) ? (
			<div className={styles.bodyView}>
				<div className={styles.menuView}>
					<ApiGroup
						apiGroup={this.state.apiGroup}
						getApiDetail={(api) => this.loadApiDetail(api)}
					/>
				</div>
				<div className={styles.detailView}>
					<div className={styles.title}>{this.state.appName}</div>
					<ApiContent apiDetail={this.state.apiDetail} />
				</div>
			</div>
		) : null;
	}
}
