import React, { Component, useEffect } from 'react';
import PropTypes from 'prop-types';

import Collapsible from 'react-collapsible';
import styles from './style.module.css';

function GroupItem({ name, index, toggleIndex }) {
	const [toggle, setToggle] = React.useState(false);

	useEffect(() => {
		if (toggleIndex === -1) {
			setToggle(false);
		} else {
			setToggle(true);
		}
	}, [toggleIndex]);

	return (
		<div className={styles.groupView}>
			<div className={styles.lblName}>{name}</div>
			<i className='material-icons'>
				{toggleIndex === index && toggle
					? 'keyboard_arrow_down'
					: 'keyboard_arrow_right'}
			</i>
		</div>
	);
}

GroupItem.propTypes = {
	index: PropTypes.number.isRequired,
	toggleIndex: PropTypes.number.isRequired,
	name: PropTypes.string.isRequired,
};

function ApiItem({ api, apiClick }) {
	let iconStyle = {};
	let iconValue = '';
	const typeName = api.type.toLowerCase();

	if (typeName === 'post') {
		iconValue = 'add_circle';
		iconStyle = styles.postIcon;
	} else if (typeName === 'get') {
		iconValue = 'near_me';
		iconStyle = styles.getIcon;
	} else if (typeName === 'put') {
		iconValue = 'update';
		iconStyle = styles.putIcon;
	} else if (typeName === 'delete') {
		iconValue = 'delete';
		iconStyle = styles.deleteIcon;
	}

	return (
		<div className={styles.itemView} onClick={apiClick}>
			<div className={styles.lblName}>{api.apiName}</div>
			<i className={'material-icons ' + iconStyle}>{iconValue}</i>
		</div>
	);
}

ApiItem.propTypes = {
	api: PropTypes.object.isRequired,
	apiClick: PropTypes.func.isRequired,
};

export default class ApiGroup extends Component {
	constructor() {
		super();
		this.state = {
			toggleIndex: -1,
		};
	}

	onToggleMenu(index) {
		if (index !== this.state.toggleIndex) {
			this.setState({ toggleIndex: index });
		} else {
			this.setState({ toggleIndex: -1 });
		}
	}

	onApiClick(api) {
		this.props.getApiDetail(api);
	}

	render() {
		return (
			<div>
				{this.props.apiGroup.map((group, i) => (
					<Collapsible
						key={i}
						open={this.state.toggleIndex === i}
						handleTriggerClick={() => this.onToggleMenu(i)}
						trigger={
							<GroupItem
								index={i}
								toggleIndex={this.state.toggleIndex}
								name={group.groupName}
							/>
						}
						transitionTime={200}
						classParentString={styles.collapsibleView}>
						{group.apiList.map((api, j) => (
							<ApiItem
								key={j}
								api={api}
								apiClick={() => this.onApiClick(api)}
							/>
						))}
					</Collapsible>
				))}
			</div>
		);
	}
}

ApiGroup.propTypes = {
	apiGroup: PropTypes.array.isRequired,
	getApiDetail: PropTypes.func.isRequired,
};
